.landing {
    display: flex;
    flex-direction: column;
    justify-content: center;
    background-image: url('https://w.wallhaven.cc/full/j5/wallhaven-j5mj9w.png');
    padding-top: 13%;
    padding-bottom: 13%;
}

.backLanding {
    display: flex;
    justify-content: center;
    align-items: center;
    /* width: 500px;
    height: 500px; */
    background-size: cover;
}

.btnLanding:hover {
    transform: scale(1.3);
    transition: transform .9s;
}

.labelLanding {
    display: flex;
    justify-content: center;
    margin-top: 5%;
    font-size: larger;
    background-color:rgba(26, 26, 26, 0.4);
    color: white;
    cursor: default;
}